.gpt3__whatgpt3-feature,
.gpt3__features-container__feature {
  

  @media screen and (max-width: 650px) {
    flex-direction: column;
  }

   @media screen and (max-width: 350px) {
    margin: 1rem 0;
    min-width: 100%;
  }
}

.gpt3__whatgpt3-feature,
.gpt3__features-container__feature-text {
  max-width: 700px;
}

.gpt3__whatgpt3-container .gpt3__features-container__feature {
  flex: 1;
  margin: 1rem;
  min-width: 210px;
  display: unset;
  flex-direction: column;
}

.gpt3__whatgpt3 {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  background: $clr-footer;

  /* ff 3.6+ */
  background: -moz-radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  /* safari 5.1+,chrome 10+ */
  background: -webkit-radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  /* opera 11.10+ */
  background: -o-radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  /* ie 10+ */
  background: -ms-radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  /* global 92%+ browsers support */
  background: radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

  &-feature {
    display: flex;
  }

  &-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem;

    @media screen and (max-width: 850px) {
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 0rem;
    }

    & > h1 {
      font-weight: 800;
      font-size: 34px;
      line-height: 45px;
      max-width: 510px;
    }
    & > p {
      font-weight: 500;
      font-size: 16px;
      line-height: 30px;
      color: $clr-subtext;
      cursor: pointer;
    }
  }

  &-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;

    @media screen and (max-width: 350px) {
      margin: 1rem 0;
      min-width: 100%;
    }
  }
}

