.gpt3__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 6rem;
  @media screen and (max-width: 768px) {
    padding: 2rem 4rem;
  }
  @media screen and (max-width: 550px) {
    padding: 2rem;
  }
  &-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    &_logo {
      margin-right: 2rem;

      & > img {
        width: 62.56px;
        height: 16.02x;
      }
    }

    &_container {
      display: flex;
      justify-content: flex-end;
      align-items: center;

      @media screen and (max-width: 1050px) {
        display: none;
      }
    }
  }

  &-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    @media screen and (max-width: 550px) {
      display: none;
    }
  }

  &-menu_container > p,
  &-sign > p,
  &-links_container > p {
    color: #fff;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
  }
  &-sign > button,
  &-menu_container > button,
  &-menu_container-links-sign > button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #ff4820;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
  }
  &-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
    color: #fff;
    & > svg {
      cursor: pointer;
    }

    @media screen and (max-width: 1050px) {
      display: flex;
    }

    &_container {
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      flex-direction: column;
      text-align: end;
      background: $clr-footer;
      padding: 2rem;
      position: absolute;
      top: 40px;
      right: 0;
      margin-top: 1rem;
      min-width: 210px;
      border-radius: 5px;
      box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);

      &-links > p {
        margin: 1rem 0;
      }

      &-links-sign {
        display: none;
        @media screen and (max-width: 550px) {
          display: block;
        }
      }
      @media screen and (max-width: 550px) {
        top: 20px;
      }
    }
  }
}
